

.result {
	display: flex;
	/*margin: 3vh 10vw;*/
	padding: 3vh 10vw;
	/*gap: 1rem;*/
	align-items: center;
	/*border: 1px solid red;*/
}

.result__img-wrapper {
	/*border: 1px solid red;*/
	height: calc(6vh + 2rem);
	width:  calc(6vh + 2rem);
	position: relative;
}

.result__img {
	height: calc(5vh + 2rem);
	max-width: calc(6vh + 2rem);
	margin-right: 1rem;
	position: absolute;
	bottom: 0;

}

.result__statistic {
	width: 61vw;
	/*padding: 1vh 0;*/
	/*border: 1px solid red;*/
}

.result__text {
	display: flex;
	justify-content: space-between;
	margin-bottom: 0.5rem;
	color: white;
}

.total-voter {
	color: #dff;
	font-size: 0.8rem;
	margin-right: 0.5rem;
}

.result__number {
	display: flex;
	gap: 10px;
	align-items: baseline;
}

.result__bar {
	/*border: 1px solid red;*/
	background-color: #888;
	height: 5vh;
	width: 61vw;
	border-radius: 5px;
}

